import React from 'react'
import {
    Text,
    Chart,
    Interval,
    Tooltip,
    Axis,
    Coordinate,
    Interaction
  } from 'bizcharts';
function Location() {
    const data = [
        { item: 'Raleigh Office', count: 70, percent: 0.4 },
        { item: 'New York Office', count: 12, percent: 0.21 },
        { item: 'Europe', count: 3, percent: 0.17 },

      ];
      const cols = {
        percent: {
          formatter: val => {
            val = val * 100 + '%';
            return val;
          },
        },
      };
      const styles ={
        mainTitle:{
          fontSize:20,
          color:"black",
          textAlign:"center"
        },
        subTitle:{
          fontSize:16,
          color:"gray",
          textAlign:"center"
        }
      }
    
    return (


        <Chart height={400} data={data} scale={cols} autoFit>
                      <p className='main-title' style={styles.mainTitle}>
    Location of my LinkedIn connected employees
  </p>  

        <Coordinate type="theta" radius={0.75} />
        <Tooltip showTitle={false} />
        <Axis visible={false} />
        <Interval
          position="percent"
          adjust="stack"
          color="item"
          style={{
            lineWidth: 1,
            stroke: '#fff',
          }}
          label={['count', {
            content: (data) => {
              return `${data.item}: ${data.percent * 100}%`;
            },
          }]}
        />
        <Interaction type='element-single-selected' />
      </Chart>
    )
}

export default Location
