import React from 'react'
import {withRouter} from 'react-router-dom'
import { Layout, Menu, Breadcrumb, Dropdown,Avatar, message,Badge} from 'antd'
import 'antd/dist/antd.css';
import {Routes} from './routes'
// import Icon, { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';

import {PieChartOutlined,LineChartOutlined,BarChartOutlined} from '@ant-design/icons';
// import MenuItem from 'antd/lib/menu/MenuItem';
const { Header, Content, Footer, Sider } = Layout;
function Frame(props) {
    return (
        <Layout>
    <Header style={{
        backgroundColor:'#00C0BA',
        height: '20vh',
    }}>
        <p style={{fontSize:40, padding: '40px 45px'}}>Wenxin Zeng's 2-Week Internship Data Analysis and Visualization</p>

    </Header>
    <Content style={{ padding: '0 50px' }}>

      <Layout className="site-layout-background" style={{ padding: '16px' }}>
        <Sider className="site-layout-background" width={200}>
          <Menu
            mode="inline"
            defaultSelectedKeys={['1']}
            defaultOpenKeys={['sub1']}
            style={{ height: '100%' }}
          >
            {Routes.map(route=>{
                const iconList=[<PieChartOutlined />,<LineChartOutlined />,<LineChartOutlined />,<BarChartOutlined />];
                return (<Menu.Item key={route.path} onClick={e=>props.history.push(e.key)}>
                    {iconList[route.icon]}
                    {route.title}
                </Menu.Item>)
            })}
          </Menu>
        </Sider>
        <Content style={{ padding: '0 24px', minHeight: 280 }}>{props.children}</Content>
      </Layout>
    
    </Content>
    <Footer style={{ textAlign: 'center' }}>Cool Intern Journey!</Footer>
  </Layout>

    )
}

 export default withRouter(Frame)


