import React from 'react'
import {
    Chart,
    Interval,
    Tooltip,
    Axis,
    Coordinate
  } from 'bizcharts';

function Title() {
    const data = [
        { title: 'Vice President', population: 22 },
        { title: 'Associate Vice President', population: 15 },
        { title: 'Analyst', population: 43 },
        { title: 'Director', population: 5 },
      ];
      const styles ={
        mainTitle:{
          fontSize:20,
          
          color:"Black",
          textAlign:"center"
        },
        subTitle:{
          fontSize:16,
          color:"gray",
          textAlign:"center"
        }
      }
      
    return (
        <Chart height={500} data={data} autoFit>
             <p className='main-title' style={styles.mainTitle}>
    Title of my LinkedIn connected employees
  </p>  
        
        <Coordinate type="polar" innerRadius={0.2} />
        <Axis visible={false} />
        <Tooltip showTitle={false} />
        <Interval
          position="title*population"
          adjust="stack"
          color="title"
          element-highlight
          style={{
            lineWidth: 1,
            stroke: '#fff',
          }}
          label={['title', {
             offset: -15,
          }]}
        />
      </Chart>
    )
}

export default Title
