import React from 'react'
import {
    G2,
    Chart,
    Geom,
    Axis,
    Tooltip,
    Coord,
    Label,
    Legend,
    View,
    Guide,
    Shape,
    Facet,
    Util,
    Point,
    Interval
  } from "bizcharts";
import api from '../icons/API.png'
import so from '../icons/stack-overflow.png'
import google from '../icons/google.png'
import baidu from '../icons/baidu.png'
function troushot_time(props) {
    var data = [
        {
          name: "API Doc",
          vote: 40
        },
        {
          name: "Stack Overflow",
          vote: 20
        },
        {
          name: "Google",
          vote: 90
        },
        {
          name: "Baidu",
          vote: 60
        }
      ];
      var imageMap = {
        'API Doc': api,
        "Stack Overflow": so,
        "Google": google,
        "Baidu": baidu
      };
      const scale = {
        vote: {
          min: 0
        }
      };
      const styles ={
        mainTitle:{
          fontSize:20,
          color:"black",
          textAlign:"center"
        },
        subTitle:{
          fontSize:16,
          color:"gray",
          textAlign:"center"
        }
      }
    return (
        
        <Chart
          data={data}
          padding={[60, 20, 40, 60]}
          scale={scale}
          autoFit
          height={250}
        >
            <p className='main-title' style={styles.mainTitle}>
    Average Daily Spent Time for Troubleshooting on each channel (Minutes)
  </p>
          <Axis
            name="vote"
            labels={null}
            title={null}
            line={null}
            tickLine={null}
          />
          <Interval
            position="name*vote"
            color={["name", ["#7f8da9", "#fec514", "#db4c3c", "#daf0fd"]]}
          />
          <Tooltip />
          <Point
            position="name*vote"
            size={60}
            shape={[
              "name",
              function(name) {
                return ["image", imageMap[name]];
              }
            ]}
          />
        </Chart>
    )
}

export default troushot_time
