import React from 'react'
import {
    Chart,
    registerShape,
    Geom,
    Axis,
    Tooltip,
    Interval,
    Interaction,
    Coordinate,
  } from "bizcharts";

function Division() {
    const data = [
        {
          type: "Global Market",
          value: 12
        },
        {
          type: "Finance Data Store",
          value: 7
        },
        {
          type: "Technology",
          value: 43
        },
        {
          type: "Risk",
          value: 14
        },
        {
          type: "Audit",
          value: 9
        }
      ]; // 可以通过调整这个数值控制分割空白处的间距，0-1 之间的数值
      
      const sliceNumber = 0.01; // 自定义 other 的图形，增加两条线
      const styles ={
        mainTitle:{
          fontSize:20,
          color:"black",
          textAlign:"center"
        },
        subTitle:{
          fontSize:16,
          color:"gray",
          textAlign:"center"
        }
      }
      
      registerShape("interval", "sliceShape", {
        draw(cfg, container) {
          const points = cfg.points;
          let path = [];
          path.push(["M", points[0].x, points[0].y]);
          path.push(["L", points[1].x, points[1].y - sliceNumber]);
          path.push(["L", points[2].x, points[2].y - sliceNumber]);
          path.push(["L", points[3].x, points[3].y]);
          path.push("Z");
          path = this.parsePath(path);
          return container.addShape("path", {
            attrs: {
              fill: cfg.color,
              path: path
            }
          });
        }
      });
    return (
         
            <Chart data={data} height={400} autoFit >
                <p className='main-title' style={styles.mainTitle}>
    Division of my LinkedIn connected employees
  </p>
               <Coordinate type="theta" radius={0.8} innerRadius={0.75} />
               <Axis visible={false} />
               <Tooltip showTitle={false} />
               <Interval
                 adjust="stack"
                 position="value"
                 color="type"
                 shape="sliceShape"
               />
               <Interaction type="element-single-selected" />
             </Chart>
    )
}

export default Division
